nav {
  position: absolute;
  top: 0;
  width: 100%;
  padding: 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  animation: slideFromTop 1.5s ease-in-out;
}
nav a svg {
  width: 10rem;
  margin-left: 1rem;
  cursor: pointer;
}
nav .icons {
  margin-right: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
nav svg {
  width: 2.5rem;
  cursor: pointer;
}
.cartNumber {
  position: absolute;
  right: 5.1rem;
  top: 1.1rem;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
}
